@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  font-family: 'Open Sans', sans-serif;
}

ul, li { 
  list-style: none;
  margin: 0;
  padding: 0 0 0 1em;
}

li {
  border: 1px dotted black;
  border-width: 0 0 1px 1px; 
}

li.container {
  border-bottom: 0px;
}

li div {
  margin: 0;
  background: white;
  position: relative;
  top: 0.5em; 
}

li ul { 
  border-top: 1px dotted black; 
  margin-left: -1em;     
  padding-left: 2em; 
}

ul li:last-child ul {
  border-left: 1px solid white;
  margin-left: -17px;
}

.tree-container {
  width: 30%;
  min-width: 15em;
}

.tree-root {
  margin-left: 1em;
}

input[type=text], input[type=number] {
  -webkit-transition: all 0.30s ease-in-out;
  transition: all 0.30s ease-in-out;
  outline: none;
  padding: 3px 0px 3px 3px;
  border: 1px solid #DDDDDD;
  min-width: 3em;
}

input[type=text]:focus, input[type=number]:focus {
  box-shadow: 0 0 5px rgba(81, 203, 238, 1);
  border: 1px solid rgba(81, 203, 238, 1);
}

input[type=text]:focus:invalid, input[type=number]:focus:invalid {
  box-shadow: 0 0 5px rgba(238, 81, 81, 1);
  border: 1px solid rgba(238, 81, 81, 1);
}


input:invalid {
  border-color: red;
}

button {
  color: #444444;
  background: #F3F3F3;
  border: 1px #DADADA solid;
  padding: 5px 10px;
  border-radius: 2px;
  font-weight: bold;
  font-size: 9pt;
  outline: none;
}

button:hover {
  border: 1px #C6C6C6 solid;
  box-shadow: 1px 1px 1px #EAEAEA;
  color: #333333;
  background: #F7F7F7;
}

button:active {
  box-shadow: inset 1px 1px 1px #DFDFDF;   
}

.add-factory-form {
  padding-top: 1.5em;
}
.add-factory-container {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  width: 20em;
  font-size: .8em;
  grid-gap: 1em;
}

.add-factory-control {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
}

.add-factory-control > label {
  font-weight: bold;
  padding-left: .4em;
}

.required {
  color: red;
}

.click-input-wrapper {
  display: grid;
  grid-template-columns: 1fr 1.7em 1.7em;
}

.click-input-text {
  min-width: 2em;
  justify-self: stretch;
}

.click-input-button {
  height: 2.3em;
  width: 2.6em;
  font-size: .6em;
  padding: 0;
  border: 1px dashed grey;
  background: none;
  box-shadow: none;
  border-radius: 2px;
  cursor: pointer;
  place-self: center;
}

.click-input-label {
  display: inline-block;
  cursor: pointer;
  width: 100%;
}

.click-input-label:hover {
  outline: 1px solid blue;
}
.factory-inputs-p {
  display: grid;
  grid-template-columns: 6fr 1fr 1fr 1fr;
  background: rgb(240, 240, 240);
  border-radius: 3px;
  border: 1px solid lightgrey;
  padding-right: .3em;
  padding-top: .2em;
  padding-bottom: .2em;
}

.factory-name {
  font-weight: 700;
}

.factory-name > span {
  padding-left: .3em;
  width: calc(100% - .3em);
}

.factory-lower-bound {
  width: 100%;
  background: lightgrey;
  border-top-left-radius: 11px;
  border-bottom-left-radius: 11px;
}

.factory-lower-bound > span {
  padding-left: 1em;
  padding-right: .2em;
  width: calc(100% - 1em);
  border-right: 1px dotted grey;
  font-size: .8em;
}

.factory-upper-bound {
  width: 100%;
  background: lightgrey;
  border-top-right-radius: 11px;
  border-bottom-right-radius: 11px;
}

.factory-upper-bound > span {
  padding-right: 1em;
  padding-left: .2em;
  width: calc(100% - 1em);
  text-align: right;
  border-left: 1px dotted grey;
  font-size: .8em;
}

.factory-delete-button {
  border: none;
  cursor: pointer;
  height: 1em;
}

.factory-delete-button:hover {
  border: none;
  color: #444444;
  background: #F3F3F3;
}
